<template>
  <div class="login-container">
    <el-card header="登录" class="login-card">
      <el-form @submit.native.prevent="login">
        <el-form-item label="name">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit">登录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data(){
    return {
      model:{}
    }
  },
  methods: {
    async login(){
      const res = await this.$http.post('login',this.model)
      // sessionStorage.localStorage.tokentoken=res.data.token  //存储在服务器上 关闭浏览器就会消失
      localStorage.token = res.data.token     //存储在浏览器中的缓存
      this.$router.push('/')
      this.$message({
        type:'success',
        message:'登陆成功'
      })
    }
  },
}
</script>

<style>
.login-card{
  width:30rem;
  margin:5rem auto;
}
</style>