<template>
  <div class="about">
    <h1>{{id?"Edit":"Create"}} Hero</h1>
    <el-form label-width="100px" @submit.native.prevent="save">
      <el-tabs value="basic" type="border-card">
        <el-tab-pane label="basic" name="basic">
          <el-form-item label="name">
            <el-input v-model="model.name"></el-input>
          </el-form-item>
          <el-form-item label="head">
            <el-upload class="avatar-uploader" :action="uploadUrl" :headers="getAuthHeaders()" :show-file-list="false"
              :on-success="res => $set(model,avatar,res.url)">
              <img v-if="model.avatar" :src="model.avatar" class="avatar1">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <!-- banner -->
          <el-form-item label="banner">
            <el-upload class="avatar-uploader" :action="uploadUrl" :headers="getAuthHeaders()" :show-file-list="false"
              :on-success="res => $set(model,'banner',res.url)">
              <img v-if="model.banner" :src="model.banner" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>

          <el-form-item label="title">
            <el-input v-model="model.title"></el-input>
          </el-form-item>
          <el-form-item label="category">
            <el-select v-model="model.categories" multiple>
              <el-option v-for="item of categories" :key="item._id" :label="item.name" :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="difficult">
            <el-rate :max="9" show-score style="margin-top:0.6rem" v-model="model.scores.difficult"></el-rate>
          </el-form-item>
          <el-form-item label="skills">
            <el-rate :max="9" show-score style="margin-top:0.6rem" v-model="model.scores.skills"></el-rate>
          </el-form-item>
          <el-form-item label="attack">
            <el-rate :max="9" show-score style="margin-top:0.6rem" v-model="model.scores.attack"></el-rate>
          </el-form-item>
          <el-form-item label="survive">
            <el-rate :max="9" show-score style="margin-top:0.6rem" v-model="model.scores.survive"></el-rate>
          </el-form-item>
          <el-form-item label="顺风出装">
            <el-select v-model="model.items1" multiple>
              <el-option v-for="item of items" :key="item._id" :label="item.name" :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="逆风出装">
            <el-select v-model="model.items2" multiple>
              <el-option v-for="item of items" :key="item._id" :label="item.name" :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="使用技巧">
            <el-input type="textarea" v-model="model.usageTips"></el-input>
          </el-form-item>
          <el-form-item label="对抗技巧">
            <el-input type="textarea" v-model="model.battleTips"></el-input>
          </el-form-item>
          <el-form-item label="团战思路">
            <el-input type="textarea" v-model="model.teamTips"></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="skills" name="skills">
          <el-button size="small" @click="model.skills.push({})"><i class="el-icon-plus" />添加技能</el-button>
          <el-row type="flex" style="flex-wrap:wrap">
            <el-col :md="12" v-for="(item,index) in model.skills" :key="index">
              <el-form-item label="name">
                <el-input v-model="item.name"></el-input>
              </el-form-item>
              <el-form-item label="photo">
                <el-upload class="avatar-uploader" :action="uploadUrl" :headers="getAuthHeaders()"
                  :show-file-list="false" :on-success="res => $set(item,'icon', res.url)">
                  <img v-if="item.icon" :src="item.icon" class="avatar1">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="delay">
                <el-input v-model="item.delay"></el-input>
              </el-form-item>
              <el-form-item label="cost">
                <el-input v-model="item.cost"></el-input>
              </el-form-item>
              <el-form-item label="description">
                <el-input type="textarea" v-model="item.description"></el-input>
              </el-form-item>
              <el-form-item label="Tips">
                <el-input type="textarea" v-model="item.tips"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button size="small" type="danger" @click="model.skills.splice(index,1)">delete</el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="best partners" name="partners">
          <el-button size="small" @click="model.partners.push({})"><i class="el-icon-plus" />添加英雄</el-button>
          <el-row type="flex" style="flex-wrap:wrap">
            <el-col :md="12" v-for="(item,index) in model.partners" :key="index">
              <el-form-item label="hero">
                <!-- filterable 加了这个字段可以直接查询 -->
                <el-select v-model="item.hero" filterable>
                  <el-option v-for="hero in heroes" :key="hero._id" :value="hero._id" :label="hero.name"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="description">
                <el-input type="textarea" v-model="item.description"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button size="small" type="danger" @click="model.partners.splice(index,1)">delete</el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>
      <el-form-item style="margin-top:1rem">
        <el-button type="primary" native-type="submit">save</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    props: {
      id: { id: {} }
    },
    data() {
      return {
        items: [],
        categories: [],
        heroes: [],
        model: {
          name: '',
          avatar: '',
          scores: {},
          skills: [],
          partners: [],
          difficult: 0
        }
      }
    },
    methods: {
      async save() {
        let res
        if (this.id) {
          res = await this.$http.put(`rest/heroes/${this.id}`, this.model)
        } else {
          res = await this.$http.post('rest/heroes', this.model)
        }
        console.log('save')
        // 跳转页面
        this.$router.push('/heroes/list')
        // 弹窗
        this.$message({
          type: 'success',
          message: '保存成功'
        })
      },
      // 头像图片加载
      async afterUpload(res) {
        // this.$set(this.model,'avatar',res.url)
        // this.model.icon = res.url    这里没有响应式是因为model这个对象里没有icon这个属性这是创建操作 所以不会响应式
        this.model.avatar = res.url
      },
      async fetch() {
        const res = await this.$http.get(`rest/heroes/${this.id}`)
        console.log(res.data)
        // 这样可以先 创建一个空对象 {} 然后再将this.model的值添加到空对象去 然后在加到res.data当中
        this.model = Object.assign({}, this.model, res.data)
        // this.model = {...this.model,...res.data}
        // this.model = res.data
      },
      // 请求数据类型 射手之类的
      async fetchCategories() {
        const res = await this.$http.get(`rest/categories`)
        this.categories = res.data
      },
      // 获取装备
      async fetchItems() {
        const res = await this.$http.get(`rest/items`)
        this.items = res.data
      },
      // 获取英雄
      async fetchHeroes() {
        const res = await this.$http.get(`rest/heroes`)
        this.heroes = res.data
      },
    },
    created() {
      this.fetchItems()
      this.fetchCategories()
      this.fetchHeroes()
      this.id && this.fetch()
    },
  }
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    /* min-width: 5rem; */
    height: 5rem;
    line-height: 5rem;
    text-align: center;
  }

  .avatar {
    min-width: 5rem;
    height: 5rem;
    display: block;
  }

  .avatar1 {
    width: 5rem;
    height: 5rem;
    display: block;
  }
</style>