<template>
  <div class="about">
    <h1>{{id?"Edit":"Create"}} advertisement</h1> 
    <el-form label-width="100px" @submit.native.prevent="save">
      <el-form-item label="name">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="advertisement">
        <el-button size="small" @click="model.items.push({})"><i class="el-icon-plus"/>add advertisement</el-button>
          <el-row type="flex" style="flex-wrap:wrap">
            <el-col :md="24" v-for="(item,index) in model.items" :key="index">
              <el-form-item label="url" style="margin-top:1rem">
                <el-input v-model="item.url"></el-input>
              </el-form-item>
                <el-form-item label="photo" style="margin-top:1rem">
                <el-upload
                class="avatar-uploader"
                :action="$http.defaults.baseURL + '/upload'"
                :show-file-list="false"
                :on-success="res => $set(item,'image', res.url)">
                <img v-if="item.image" :src="item.image" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>           
              <el-form-item>
                <el-button size="small" type="danger" @click="model.items.splice(index,1)">delete</el-button>
              </el-form-item>
            </el-col>
          </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">save</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props:{
    id:{id:{}}
  },
  data(){
    return{
      model:{
        items:[]
      },
      Parents:{}
    }
  },
  methods: {
    async save(){
      let res
      if(this.id){                  
        res = await this.$http.put(`rest/ads/${this.id}`,this.model)
      }else{
        res = await this.$http.post('rest/ads',this.model)
      }
      console.log('save')
      // 跳转页面
      this.$router.push('/ads/list')
      // 弹窗
      this.$message({
        type:'success',
        message:'保存成功'
      })
    },
    async fetch(){
      const res = await this.$http.get(`rest/ads/${this.id}`)
      // this.model = res.data      一开始items为空数组 但是赋值后有没有了 所有会报错
      this.model = Object.assign({},this.model,res.data)  //将model覆盖在上面
    },
  },
  created() {
    this.id && this.fetch()
  },
}
</script>