import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import CategoryEdit from '../views/CategoryEdit.vue'
import CategoryList from '../views/CategoryList'
import ItemsEdit from '../views/ItemsEdit'
import ItemsList from '../views/ItemsList'
import HeroesEdit from '../views/HeroesEdit'
import HeroesList from '../views/HeroesList'
import ArticleEdit from '../views/ArticleEdit'
import ArticleList from '../views/ArticleList'
import AdEdit from '../views/AdEdit'
import AdList from '../views/AdList'
import AdminUserEdit from '../views/AdminUserEdit'
import AdminUserList from '../views/AdminUserList'
import Login from '../views/Login'
Vue.use(VueRouter)

const routes = [
  {
    path: '/login', name: 'login', component: Login, meta: { isPublic: true }
  },
  {
    path: '/',
    name: 'main',
    component: Main,
    children: [
      { path: '/categories/create', component: CategoryEdit },
      { path: '/categories/edit/:id', component: CategoryEdit, props: true },
      { path: '/categories/list', component: CategoryList },

      { path: '/items/create', component: ItemsEdit },
      { path: '/items/edit/:id', component: ItemsEdit, props: true },
      { path: '/items/list', component: ItemsList },

      { path: '/heroes/create', component: HeroesEdit },
      { path: '/heroes/edit/:id', component: HeroesEdit, props: true },
      { path: '/heroes/list', component: HeroesList },

      { path: '/articles/create', component: ArticleEdit },
      { path: '/articles/edit/:id', component: ArticleEdit, props: true },
      { path: '/articles/list', component: ArticleList },

      { path: '/ads/create', component: AdEdit },
      { path: '/ads/edit/:id', component: AdEdit, props: true },
      { path: '/ads/list', component: AdList },

      { path: '/admin_user/create', component: AdminUserEdit },
      { path: '/admin_user/edit/:id', component: AdminUserEdit, props: true },
      { path: '/admin_user/list', component: AdminUserList },
    ]
  }
]

const router = new VueRouter({
  routes
})
// beforeEach 表示在你每次切换路由时 你要做什么   客户端路由限制
// beforeEach((to,from,next) => {})   to：即将进入的目标  from：正要离开的页面  next用来调用resolve这个钩子
router.beforeEach((to, from, next) => {
  // 只有进入了login界面才有isPublic
  if (!to.meta.isPublic && !localStorage.token) {
    return next('/login')
  }
  next()
})

export default router
